import styled from "styled-components";

export const Wrapper = styled.div`
    background: linear-gradient(
        to bottom, 
        rgba(0, 0, 0, 0.2) 10%,
        rgba(0, 0, 0, 0.4) 70%,
        rgba(21, 21, 23, 0.98) 100%
    ), url(${({ image }) => image}), #1c1c1c;
    background-size: 100%, cover;
    background-position: center;
    position: relative;
    height: 640px;
`;

export const Content = styled.div`
    padding: 20px;
    max-width: var(--maxWidth);
    margin: 0 auto;

`;

export const Text = styled.div`
    max-width: 600px;
    position: absolute;
    bottom: 180px;
    margin: 0 100px;
    min-height: 100px;
    color: var(--white);
    h1{
        font-size: 3rem;
        margin: 0;

        @media screen and (max-width: 720px){
            font-size: var(--fontBig);
        }
    }
    p{
        font-size: var(--fontMed);
        @media screen and (max-width: 720px){
            font-size: var(--fontSmall);
        }
    }
    span{
        margin-left: 10px;
    }
    #ratings{
        font-weight: 800;
    }
    .red{
        color: red;
        font-size: 1rem;
        font-weight: 800;
    }

    @media screen and (max-width: 720px){
        max-width: 100%;
        margin: 0 5px;
    }
`;

export const Button = styled.button`
    width: 100px;
    height: 38px;
    border-radius: 35px;
    border: none;
    color: var(--white);
    font-weight: 600;
    background: ${({bgColor})=>bgColor};
    margin-right: 15px;
    cursor: pointer;
    :focus{
        outline: none;
    }
    :hover{
        opacity: 0.9;
    }
    :disabled{
        opacity: 0.7;
        cursor: default;
    }
    .btnIcon{
        margin-right: 7px;
    }
`;