import React from 'react';
import Thumb from '../Thumb';
//icon
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlay } from '@fortawesome/free-solid-svg-icons';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
//config
import { IMAGE_BASE_URL, POSTER_SIZE } from '../../config';
import { Wrapper, Content, Text, Button } from './MovieInfo.styles';
import NoImage from '../../images/no_image.jpg'
import { Link } from 'react-router-dom';

const MovieInfo = ({movie, disabled})=>(
    <Wrapper backdrop={movie.backdrop_path}>
        <Content>
            <div className='thumb'>
            <Thumb image={
                movie.poster_path 
                ? `${IMAGE_BASE_URL}${POSTER_SIZE}${movie.poster_path}`
                : NoImage
            }
            clickable={false}
            />
            </div>
            <Text>
                <h1>{movie.title}</h1>
                <p>{movie.overview}</p>
                <Button bgColor="#DC1623" disabled={disabled} onClick={()=>{
                    window.open(`${movie.homepage}`);
                    console.log(movie.homepage);
                    console.log(disabled);
                }}>
                    <FontAwesomeIcon icon={faPlay} className="btnIcon"/>Netflix
                </Button>
                <Button bgColor="#010000"><FontAwesomeIcon icon={faPlus} className="btnIcon"/>Add List</Button>
            </Text>
        </Content>
    </Wrapper>
);

export default MovieInfo;