import styled from "styled-components";

export const Wrapper = styled.div`
    display: flex;
    align-items: center;
    border-radius: 20px;
    border: 1px solid rgba(255, 255, 255, 0.5);
    background: rgba(50, 50, 50, 0.23);
    padding: 0 15px;
`;
export const Input = styled.input`
    max-width: 300px;
    height: 30px;
    background: none;
    border: none;
    display: ${({focus})=>focus};
    color: var(--white);

    ::placeholder{
        color: rgb(190, 190, 190);
    }
    :focus {
        outline: none;
    }
`;
export const Logo = styled.div`
    width: 30px;
    height: 30px;
    display: flex;
    cursor: pointer;
    justify-content: center;
    align-items: center;
`;