import React from 'react';
import { useParams } from 'react-router';

import { BACKDROP_SIZE, IMAGE_BASE_URL, POSTER_SIZE } from '../config';
//components
import Grid from './Grid';
import Thumb from './Thumb';
import HeroImage from './HeroImage';
//icon
import {ReactComponent as TrendingLogo} from '../images/trending.svg';
import popularIcon  from '../images/tmdb_logo.svg';
import NoImage from '../images/no_image.jpg';
//fetch
import { useHomeFetch } from '../hooks/useHomeFetch';
import { useMovieFetch } from '../hooks/useMovieFetch';

const Home = () => {
    const {
        state, 
        loading, 
        error,
        searchTerm
    } = useHomeFetch();   
    console.log(state);
    // const { movieId } = useParams();

    let movieId = state.results[0] != undefined ? state.results[0].id : '497698';
    const movie =  useMovieFetch(movieId);
    console.log(movie);


    if(error) return <div>Something went wrong...</div>
    return (
        <>
            
            { !searchTerm && state.results[0] ? (
                <HeroImage 
                    image={`${IMAGE_BASE_URL}${BACKDROP_SIZE}${state.results[0].backdrop_path}`}
                    title={state.results[0].original_title}
                    text={state.results[0].overview}
                    ratings={state.results[0].vote_average}
                    duration={movie.state.runtime}
                    tagline={movie.state.tagline}
                    homepage={movie.disabled}
                />
            ) : null }
            <Grid header="Popular Now" icon={(<TrendingLogo fill="white" width="40px" height="30px"/>)}>
                {state.results.map(movie => (
                    <Thumb
                        key={movie.id}
                        clickable
                        image={movie.poster_path
                        ? IMAGE_BASE_URL + POSTER_SIZE + movie.poster_path
                        : NoImage}
                        title={movie.title}
                        releaseDate={movie.release_date}
                        movieId={movie.id}
                    />
                ))}
            </Grid>
        </>
    );
}

export default Home;