import { useState, useEffect } from "react";

import API from '../API';

export const useMovieFetch = movieId =>{
    const [state, setState] = useState({});
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(false);
    const [disabled, setDisabled] = useState(false);

    useEffect(()=>{

        const fetchMovie = async () => {
            try{
                setLoading(true);
                setError(false);

                const movie = await API.fetchMovie(movieId);
                const credits= await API.fetchCredits(movieId);
                //directors only
                const directors = credits.crew.filter(
                    member => member.job === 'Director'
                );

                setState({
                    ...movie,
                    actors: credits.cast,
                    directors
                });

                let domain = (new URL("https://www.netflix.com/").hostname);
                if(new URL(movie.homepage).hostname !== domain){
                    setDisabled(true);
                }
                else{
                    setDisabled(false);
                }
                
                setLoading(false);

            }catch(error){
                setError(true);
            }
        }
        // const isDisabled= async ()=>{
            
        //     try {
        //         let domain = (new URL("https://www.netflix.com/").hostname);
        //         if(new URL(state.homepage).hostname != domain){
        //             isDisabled(true);
        //         }
        //         isDisabled(false);
        //     }
        //     catch(error) {
        //         isDisabled(true);
        //     }
        // }

        fetchMovie();
    }, [movieId]);

    return {state, loading, error, disabled};
}