import React from 'react';
import {calcTime} from '../../helpers';
//icon
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faStar } from '@fortawesome/free-solid-svg-icons';
import { faPlay } from '@fortawesome/free-solid-svg-icons';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import PropTypes from 'prop-types';
//style
import { Wrapper, Content, Text, Button } from './HeroImage.styles';

const HeroImage = ({image, title, text, ratings, duration, homepage}) => (
    <Wrapper image={image}>
        <Content>
            <Text>
                <p>Duration:  <span className="red">{ calcTime(duration) }</span></p>
                <h1>{title}</h1>
                <p>{text}</p>
                <Button bgColor="#DC1623" disabled={homepage}>
                    <FontAwesomeIcon icon={faPlay} className="btnIcon"/>Netflix
                </Button>
                <Button bgColor="#010000"><FontAwesomeIcon icon={faPlus} className="btnIcon"/>Add List</Button>
                <FontAwesomeIcon icon={faStar} color="#F3C208"/> 
                <span id='ratings'>{ratings}</span>
            </Text>
           
        </Content>
    </Wrapper>
);

HeroImage.propTypes = {
    image: PropTypes.string,
    title: PropTypes.string,
    text: PropTypes.string
}

export default HeroImage;