export const Wrapper = styled.div`
    color: whitesmoke;
    line-height: 15px;
    border-radius: 12px;
    position: relative;
    a{
        text-decoration: none;
    }
    #text{
        margin: -4px 15px 0 7px;
        display: ${({showText})=>showText? 'block' : 'none'};
    }
    #title{
        font-weight: 600;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        word-break: break-all;
        color: white;
    }
    #releaseDate{
        color: gray;
        margin-top: -9px;
        font-size: 14px;
    }

    :hover{
        box-shadow: ${({showText})=>showText? '0 6px 9px rgba(20, 20, 20, 1)' : 'none'};
    }
`;


export const Image = styled.img`
    width: 100%;
    max-width: 720px;
    transition: all 0.3s;
    object-fit: cover;
    border-radius: 12px;
    cursor: pointer;
`;