import React from 'react';
import {  Wrapper, Image } from './Thumb.styles';
import { Link } from 'react-router-dom';

const Thumb = ({movieId, image, title, releaseDate, clickable}) => (
    <Wrapper showText={clickable}>
        {clickable ? 
            (<Link to={`/${movieId}`}>
                <Image src={image}/>
                <div id="text">
                    <p id="title">{title}</p>
                    <p id='releaseDate'>{releaseDate}</p>
                </div>
            </Link>) : (
                <Image src={image}/>
            )
        }
    </Wrapper>
);

export default Thumb;