import React, {useState}from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSearch } from "@fortawesome/free-solid-svg-icons";
import { Wrapper, Input, Logo } from "./SearchBar.styles";

const SearchBar = ({focus = false}) => {
    var [spread, setState] = useState(false);
    return(
        <Wrapper>
            <Input focus={spread===true ? "flex":"none"} placeholder="Search Movie"/>
            <Logo>
                <FontAwesomeIcon icon={faSearch} color="white" onClick={()=>{
                   if(spread === true){
                       setState(false);
                   }else{
                       setState(true);
                   }
                }}/>
            </Logo>
        </Wrapper>
    );
}
export default SearchBar;