import React, { useState, useEffect } from 'react';
import NavBar from '../NavBar';
//style
import { Wrapper, Content, Logo } from './Header.styles';
import { GlobalStyle } from '../../GlobalStyle';
//style fetch

const Header = () => {

    var [isTrans, setTrans] = useState(window.screenY === 0 ? "trans" : "solid") ;
    useEffect(()=>{
        window.addEventListener("scroll", ()=>{
            if(window.scrollY === 0) { 
                setTrans("trans");
            }
            else {
                setTrans("solid");
            }
            console.log(isTrans);
        });
    }, []);
    return(
    <Wrapper className={isTrans}>
        <Content>
            <Logo src="https://fontmeme.com/permalink/210813/1e57ace8e1fe199404b84c8ecc9e3b1b.png" />
            <NavBar/>
        </Content>
    </Wrapper>
    );
};

export default Header;