import styled from "styled-components";
import { IMAGE_BASE_URL, BACKDROP_SIZE } from "../../config";
export const Wrapper = styled.div`
    background: ${({backdrop}) => 
        backdrop ? 
        `linear-gradient(
            to bottom, 
            rgba(0, 0, 0, 0.4) 10%,
            rgba(0, 0, 0, 0.7) 60%,
            rgba(21, 21, 23, 1) 100%
        ), url(${IMAGE_BASE_URL}${BACKDROP_SIZE}${backdrop}), #1c1c1c`
        : '#000'
    };
    background-size: cover;
    background-position: center;
    height: 500px;
    padding: 20px 50px;
    position: relative;
    margin-top: 56px;

    /* justify-content: center; */
    /* align-items: center; */

    @media screen and ( max-width: 768px ){
    padding: 20px 20px;
        
    }
`; 

export const Content = styled.div`
    display: flex;
    margin: 0 auto;
    max-width: var(--maxWidth);
    /* background: rgba(0, 0, 0, 0.2); */
    border-radius: 20px;
    
/* 
    left: 0;
    right: 0;
    position: absolute; */
    .thumb{
        width: 50%;
    }

    @media screen and ( max-width: 768px ){
        display: block;
        max-height: none;
        .thumb{
            width: 100%;
        }
    }

`;

export const Text = styled.div`
    width: 100%;
    padding: 20px 40px;
    color: var(--white);
    overflow: hidden;
`;

export const Button = styled.button`
    width: 100px;
    height: 38px;
    border-radius: 35px;
    border: none;
    color: var(--white);
    font-weight: 600;
    background: ${({bgColor})=>bgColor};
    margin-right: 15px;
    cursor: pointer;
    :focus{
        outline: none;
    }
    :hover{
        opacity: 0.9;
    }
    :disabled{
        opacity: 0.7;
        cursor: default;
    }
    .btnIcon{
        margin-right: 7px;
    }
`;