import { createGlobalStyle } from 'styled-components';

export const GlobalStyle = createGlobalStyle`
    :root{
        --maxWidth: 1280px;
        --headerBG: rgb(21, 21, 23);
        --white: #ffffff;
        --lightGrey: #eee;
        --medGrey: #353535;
        --darkGrey: #1c1c1c;
        --fontSuperBig: 2.5rem;
        --fontBig: 1.5rem;
        --fontMed: 1.2rem;
        --fontSmall: 1rem;
    }
    body{
        padding: 0;
        margin: 0;
        background: var(--headerBG);
        h1{
            font-size: 2rem;
            font-weight: 600;
            color: var(--white);
        }

        h3{
            font-size: 1rem;
            color: var(--white);
        }
    }
    .trans{
        background: none;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        animation: trans 1s;
        @keyframes trans{
            from{
                background: var(--headerBG);
            }to{
                background: rgba(21, 21, 23, 0);
                border-bottom: 1px solid rgba(255, 255, 255, 0.1);
            }
        }
    }
    .solid{
        background: linear-gradient(to bottom, rgba(21, 21, 23, .99) 70%, rgba(21, 21, 23, .96) 85%, rgba(21, 21, 23, .92) 100%);
        box-shadow: 0 3px 5px rgba(20, 20, 20, 0.5);
        animation: solid 1s;
        @keyframes solid{
            from{
                background: none;
                box-shadow: none;
            }to{
                background: var(--headerBG);
            }
        }
    }
    *{
        box-sizing: border-box;
        font-family: 'Abel', sans-serif;
    }
    
`;